<template>
    <div style="width:100%;overflow:hidden;position:relative;height:100vh;">
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">意见反馈</p>
        </div>
        <textarea name="" id="" class="textarea f26" v-model="content" placeholder="请输入您的反馈意见，我们亲耳恭听....." cols="30" rows="10"></textarea>
        <div class="centerBtn f26 cfff text-center" @click="submit">提交</div>
    </div>
    
</template>

<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            content : ''
        }
    },
    methods : {
        returns(){
            this.$router.go(-1);//返回上一层
        },
        submit(){
            if(this.content == ''){
                this.$toast('反馈不能为空！');
                return
            }
            this.$http({url:this.$api.my.addFeedback},{content:this.content,origin:2,submitId:JSON.parse(localStorage.getItem('zhxy_user')).id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    this.$toast('提交成功');
                    this.$router.push({path:'/setting'});
                }else{
                    this.$toast('提交失败');
                }
            })
        }
    }
}
</script>

<style scoped>
    .msgBox{width: 100%;overflow: hidden;}
    .msgLine{margin-left: 0.3rem;border-bottom: 1px solid #f2f2f2;overflow: hidden;height: 1rem;line-height: 1rem;}
    .msgTitle{float: left;}
    .msgHead{width: 0.7rem;height: 0.7rem;display: block;float:right;border-radius: 50%;margin: 0.15rem 0.2rem 0 0;}
    .msgTo{width: 0.3rem;height: 0.3rem;float: right;margin: 0.35rem 0.3rem 0 0;}
    .textarea{width: 92%;min-height: 4.8rem;max-height:10rem;background: #f3f3f3;border-radius: 7px;margin: 0.3rem auto;display: block;border: none;padding: 0.3rem;box-sizing: border-box;outline: none;}
    .centerBtn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: absolute;bottom: 0.6rem;left: 50%;margin-left: -3.45rem; }
</style>


